
body, div, span, header, footer, nav, section, aside, article, ul, dl, dt, dd, li, a, p, h1, h2, h3, h4, h5, h6, i, b, textarea, button, input, select, figure, figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  font-weight: normal;
  font-family: "Arial";
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}
body:hover, div:hover, span:hover, header:hover, footer:hover, nav:hover, section:hover, aside:hover, article:hover, ul:hover, dl:hover, dt:hover, dd:hover, li:hover, a:hover, p:hover, h1:hover, h2:hover, h3:hover, h4:hover, h5:hover, h6:hover, i:hover, b:hover, textarea:hover, button:hover, input:hover, select:hover, figure:hover, figcaption:hover {
    outline: none;
}
html, body {
  width: 100%;
  height: 100%;
  background: #f2f2f2;
}
.wrap {
  position: relative;
}

.index {
  width: 100%;
  height: 100%;
  font-size: 0;
  line-height: 0;
  color: #666;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  position: absolute;
  right: 0;
  left: 0;
  height: 100%;
}
.index .ball-container .ball {
    position: fixed;
    left: 32px;
    bottom: 22px;
    z-index: 200;
    transition: all 0.4s linear;
}
.index .ball-container .ball .inner {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: #00a0dc;
      transition: all 0.4s linear;
}
.nav {
  position: fixed;
  left: 0;
  width: 1.8rem;
}
.nav a {
    display: block;
    width: 100%;
    font-size: .2rem;
    line-height: .86rem;
    color: #333333;
    background: #fff;
    text-align: center;
}
.nav a.active {
      background: #00ae66;
      color: #fff;
}
.content {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 4.6rem;
  height: 100%;
}
.content .content-box {
    white-space: nowrap;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.content .content-box .content-item {
      position: relative;
      height: 5rem;
      font-size: .3rem;
      line-height: 3.2rem;
      text-align: center;
      color: #ccc;
      border-bottom: 1px solid #fff;
}
.content .content-box .content-item .add {
        position: absolute;
        right: .2rem;
        bottom: .2rem;
        font-size: .2rem;
        line-height: .3rem;
}
.cart {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 1rem;
  background: #fff;
}
.cart .num {
    position: absolute;
    left: .4rem;
    top: -.3rem;
    width: 1.1rem;
    height: 1.1rem;
    background: #00ae66;
    border-radius: 50%;
    border: 3px solid #fff;
    font-size: .4rem;
    line-height: 1rem;
    text-align: center;
    color: #fff;
}

.wrap {
  z-index: 111;
  background: #fff;
  font-size: .2rem;
  line-height: .2rem;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
footer {
  left: 0;
  right: 0;
  bottom: 0;
  height: 50px;
  box-shadow: 0 2px 8px #ccc;
  position: absolute;
  background-color: #fff;
}
.ball-group {
  position: absolute;
  width: 10px;
  height: 10px;
  margin: auto;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
}
